import {
POST_LOADED,
POST_LOADING,
SINGLEPOST_LOADING,
SINGLEPOST_LOADED,
CREATING_COMMENT,
CREATED_COMMENT
} from "../action/types";

  const initialState = {
    //  token:localStorage.getItem("token"),
    //  isAuthenticated:null,
    isLoading: false,
    posts:[],
    comments:[],
    singlePost:''
  };
  
  export default function (state = initialState, action) {
    switch (action.type) {
  
      case POST_LOADED:
        // console.log("ACTION PAYLOAD", action.payload);
        return {
          ...state,
          // isAuthenticated:true,
          isLoading: false,
          posts:action.payload
        };
        case POST_LOADING:
            return {
              isLoading: true,
            };  

        case SINGLEPOST_LOADED:
          // console.log("ACTION PAYLOAD", action.payload);
          return {
            ...state,
            // isAuthenticated:true,
            isLoading: false,
            singlePost:action.payload
          };
          case SINGLEPOST_LOADING:
              return {
                isLoading: true,
              };  
          case CREATED_COMMENT:
            // console.log("ACTION PAYLOAD", action.payload);
            return {
              ...state,
              // isAuthenticated:true,
              isLoading: false,
              comments:action.payload
            };
            case CREATING_COMMENT:
                return {
                  isLoading: true,
                };  
  
              
              

      default:
        return state;
    }
  }
  