import React from "react";

import { 
    Link,
    Route,
    Switch, 
    withRouter,
    BrowserRouter as Router } from "react-router-dom"
import Logout from "./HomeLogout"

import {connect} from 'react-redux'

class Header extends React.Component {

    logoutFunction = async  () =>{
        await this.props.logout() 
        this.props.history.push('/')     
        // console.log("LOGOUT BUTTON CLICKED!!")
    }


    componentDidMount(){

        
        const {user}  = this.props.auth

  
        if(user ){
            if (user.message){      console.log("user error")
                this.logoutFunction()
            }
        }
    }
    
    

    render(){


        const {user}  = this.props.auth
        return(
    
            <header className="main_menu home_menu" style={{marginBottom:"0"}}>
            <div className="container mt-0">
                <div className="row align-items-center justify-content-center">
                    <div className="col-lg-12">
                        <nav className="navbar navbar-expand-lg navbar-light mb-0 mt-0" >
                            <Link className="navbar-brand" to="/"> 
                            <img src="/elesarrdp.jpg" 
                            className="elesarr" 
                            alt="logo" 
                            // width="150" 
                            />
                             </Link>
                            <button className="navbar-toggler" type="button" data-toggle="collapse"
                                data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent"
                                aria-expanded="false" aria-label="Toggle navigation">
                                <span className="menu_icon"><i className="fas fa-bars"></i></span>
                            </button>
    
                            <div className="collapse navbar-collapse main-menu-item" id="navbarSupportedContent">
                                <ul className="navbar-nav">
                                    <li className="nav-item">
                                        <Link className="nav-link" to="/">Home</Link>
                                    </li>
 
                                    <li className="nav-item">
                                        <Link className="nav-link"  to="/about">About Us</Link>
                                    </li>
                                    
    
                                
                                {user && user.class == "Individual" ? 

                                <li className="nav-item">
                                <Link className="nav-link" to="/dashboard">Dashboard</Link>
                            </li>: null}
                            {user && user.class == "Organisation" ? 

                            <li className="nav-item">
                            <Link className="nav-link" to="/organisation/dashboard">Dashboard</Link>
                            </li>: null}

                                    <li className="nav-item">
                                        <Link className="nav-link" to="/campaigns">Discover</Link>
                                    </li>
                                    <li className="nav-item">
                                    <Link className="nav-link" to="/how-elesarr-work">How elesarr works</Link>
                                    </li>
                                    <li className="nav-item">
                                    <Link className="nav-link" to="/blog">Blog</Link>
                                    </li>
                                       
                                    {this.props.auth.isAuthenticated ? 
                                    <Logout /> :                                     <li className="nav-item">
                                    <Link className="nav-link" to="/login">Login</Link>
                                </li>
                                } 

                                {/* {user && user.message=="error" ? this.logoutFunction :null} */}
                                {/* {user && user.message=="error" ? this.logoutFunction() :null} */}

                                </ul>
                            </div>
                        </nav>
                    </div>
                </div>
              </div>
         </header>
        )
    }
   
}
const mapStateToProps = state => ({
    auth: state.auth,
    error: state.error
  });
  
  export default connect(mapStateToProps)(
    withRouter(Header)
  );
