import React, { Component } from "react";
import CryptoJS from "crypto-js";
// import "./confirmToken.css";
import { Link, withRouter, BrowserRouter as Router } from "react-router-dom";
class ConfirmToken extends Component {
  async componentDidMount() {
    let s = new URLSearchParams(window.location.search).get("s");

    let replaced = s.replace("-" , "+")
    let replaced2 = replaced.replace("-" , "+")
    console.log("s ", replaced2);
// just decrypts the data passed via the url
    var decrypted = await CryptoJS.AES.decrypt(replaced2.toString(), "Loaded888");
    console.log("decrypted ", decrypted);
    var plaintext = await decrypted.toString(CryptoJS.enc.Utf8);
    console.log(plaintext);
    //    console.log( "code ", plaintext)

    await this.setState({
      code: plaintext,
      encryptedCode: s,
    });
  }

  state = {
    code: "",
    encryptedCode: "",

    enteredCode: null,
  };

  onChange = (e) => {
    this.setState({
      [e.target.name]: e.target.value,
    });
  };

  verify = (e) => {
    e.preventDefault();
    const { enteredCode, code } = this.state;

    if (enteredCode !== code) {
      console.log(enteredCode, code);
      alert("Invalid Code");
    } else {
      let s = new URLSearchParams(window.location.search).get("s");
      let id = new URLSearchParams(window.location.search).get("id");
      let code = this.state.code;
      this.props.history.push({
        pathname: `/confirmPassword/?s=${s}&d=${code}&id=${id}`,
      });
    }
  };

  componentDidUpdate(prevProps) {
    const { code, encryptedCode } = this.state;
    // console.log(prevProps)
    if (code != null || "") {
      console.log("code , ", code);
    }
  }
  render() {
    return (
      //

      <div className="row justify-content-center">
        <div className="col-xl-10 col-lg-12 col-md-9">
          {/* <div class="elesarrloading"></div> */}

          <div className="card o-hidden border-0 shadow-lg my-5">
            <div className="card-body p-0">
              {/* <!-- Nested Row within Card Body --> */}
              <div className="row">
                <div className="col-lg-6 d-none d-lg-block bg-login-image"></div>
                <div className="col-lg-6">
                  <div className="p-5">
                    {/* {this.props.error.status === 400 ? 
                        <p style={{color:'red', fontSize:"30px"}} className="text-center">
                          {this.props.error.msg.msg} !!                        </p>
  
                : null} */}

                    <div className="text-center">
                      <h1 className="h4 text-gray-900 mb-4">
                        Enter Code Sent To Your Email
                      </h1>
                    </div>
                    <form className="user" onSubmit={this.verify}>
                      <div className="form-group">
                        <input
                          type="text"
                          className="form-control form-control-user"
                          onChange={this.onChange}
                          id="text"
                          name="enteredCode"
                          placeholder="code"
                          required
                        />
                      </div>

                      <button
                        type="submit"
                        className="btn_3 btn-block text-center"
                      >
                        Confirm
                      </button>
                      <hr />
                    </form>
                    <hr />
                    {/* <div className="text-center">
                  <a className="small" href="#">Forgot Password?</a>
                </div> */}
                    <div>
                      Didn't receive the code?
                      <br />
                      <a href="#" onClick={this}>
                        Send code again
                      </a>
                      <br />
                      <a
                        href="#"
                        onClick={() => this.props.history.push("/confirmEmail")}
                      >
                        Change Email
                      </a>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}
export default withRouter(ConfirmToken);
