import axios from 'axios';
import {
    PAYOUT_CREATING_LOADING,
    PAYOUT_CREATED_LOADED,
    PAYOUT_LOADING,
    PAYOUT_LOADED,
    PAYOUT_LENGTH_LOADED,
PAYOUT_LENGTH_LOADING
} from './types';



var location = window.location.href

if(location.slice(0,17)== 'http://localhost:'){
  axios.defaults.baseURL = 'https://elesarrdevelopment.herokuapp.com';
}else{
  axios.defaults.baseURL = 'https://eles-api.herokuapp.com'
}


// GET A USERS FOR A PARTICULAR PROJECT
export const getPayoutRequests = () => dispatch => {
    dispatch({
      type:  PAYOUT_LOADING
    });
  
    axios
      .get(`/api/payout`)
      .then(res =>{
        dispatch({
          type:PAYOUT_LOADED,
          
          payload: res.data
        })
        console.log(res.data)
      }
      )
      .catch(err => {
        // console.log(err);
        console.log("ERR")
      });
  };

  export const getTotalPayoutRequests = () => dispatch => {
    dispatch({
      type:  PAYOUT_LENGTH_LOADING
    });
  
    axios
      .get(`/api/payout/requests/length`)
      .then(res =>{
        dispatch({
          type:PAYOUT_LENGTH_LOADED,
          
          payload: res.data
        })
        // console.log(res.data)
      }
      )
      .catch(err => {
        // console.log(err);
        console.log("ERR")
      });
  };

  
  export const addPayoutRequests = ({
    email, 
      amount, 
      userID, 
      projectID,
      accountNO,
      bankName

    }) => dispatch => {
    
    const config = {
        headers: {
        // 'Accept': 'application/json',
        'Content-Type': 'application/json'
    },
    };
      // Request body
      const body = JSON.stringify({
        email, 
      amount, 
      userID, 
      projectID,
      accountNO,
      bankName
    });
    
    dispatch({
      type:  PAYOUT_CREATING_LOADING
    });
  

    axios
      .post(`/api/payout`,body, config)
      .then(res =>{
        dispatch({
          type:PAYOUT_CREATED_LOADED,
          
          payload: res.data
        })
        // console.log(res.data)
        window.location.href = "/dashboard"
      }
      )
      .catch(err => {
        // console.log(err);
        console.log(err.response.data)
      });
  };
