import React from 'react';
import Fragment from "../../hoc/Fragment"
import {login} from '../../action/authAction'
import axios from "axios"
import CryptoJS from "crypto-js"
import {connect} from 'react-redux'
import { 
    Link,
    withRouter,
    BrowserRouter as Router } from "react-router-dom"


import Lottie from "react-lottie";
import * as CampaingLoading from '../../loaders/loading.json'

    
    
    const defaultOptions = {
       loop: true,
       autoplay: true,
       animationData: CampaingLoading.default,
       rendererSettings: {
         preserveAspectRatio: "xMidYMid slice",
       },
     };

    //  <Lottie
    //     options={defaultOptions}
    //     height={"100%"}
    //     width={"100%"}
    //   /> 


class ConfirmEmail extends React.Component {

    state = {
        email : null,
        loading:false
    }


    componentDidMount(){
      
        const { error, isAuthenticated } = this.props.auth;
       
    
      }
      

    onChange = e => {
        this.setState({ [e.target.name]: e.target.value });
      };


    confirmEmail = async e =>{
    //   this.setState({loading:true})
        e.preventDefault()
        this.setState({loading:true})
        const email = this.state.email

        const config = {
            headers: {
          // 'Accept': 'application/json',
          'Content-Type': 'application/json'
        },
        };
        
        // console.log(config)
        
        // Request body
        const body = JSON.stringify({email});
        // console.log("registering individual")
          console.log("consoling the body from register action ",body)
          axios.post('/api/login/forgotPassword', body, config)
          .then(res =>{
            this.setState({loading:false})

               console.log(res.data)
               var encrypted = (CryptoJS.AES.encrypt(`${res.data.code}`, "Loaded888")).toString();
               var replaced = encrypted.replace("+", "-");
               var replaced2 = replaced.replace("+", "-");
                console.log("replaced ", replaced2)
               
               if(res.data.code){
                   
                   this.props.history.push(
                       
                    {
                        pathname: `/confirmToken/?s=${

                            replaced2}&id=${res.data.id}`,
                        code: res.data.code,
                      }
                    
                   )
               }
              })
              // history.push('/dashboard')
         
        
          
          .catch(err => {
            this.setState({loading:false})
            console.log(err)
           
          });

        // const password = this.state.password

        // const newUser = {
        //     email: email
        //     ,password:password
            
        // }
        // console.log("details ", newUser)

     
    }


    componentDidUpdate(prevProps) {

        const { error, isAuthenticated ,user} = this.props.auth;
        if(user && user.length !== 0){
          // console.log("logged in user class ", user.user.class)
          
        }

        if(user && user.length !== 0 && user.class === "Organisation" ){
          // this.props.history.push('/organisation/dashboard')
          window.location.href="/organisation/dashboard"
          this.setState({loading:false})
        }

        else if(user && user.length !== 0 &&  user.class === "Individual" ){
          // this.props.history.push({
          //   pathname:'/dashboard',
          // user:user.user.id})
          window.location.href="/dashboard"
          this.setState({loading:false})
        }
        if (error && error !== prevProps.error) {
          //
          if (error.id === "REGISTER_FAIL") {
            // this.setState({
            //   msg: error.msg.msg
            // });
            console.log({ msg: error.msg.msg})
          } else if (error.id === "LOGIN_FAIL") {
            this.setState({
              msg: error.msg.msg
            });
            console.log({ msg: error.msg.msg})
          } else if (error.id === "REGISTER_SUCCESS") {
            // this.props.history.push("/sponsor")
            
            // window.location.reload()
            console.log("register success")
          }
    
          else if (error.id === "LOGIN_SUCCESS") {
            // this.props.history.push("/sponsor")
    
            console.log("logged in successfully")
            // window.location.reload()
          }
          
          
          else {
            this.setState({
              msg: null
            });
          }
        }
    
      }

    render(){
      // console.log(this.props.error)
        return(
        
            // <!--================login_part Area =================-->
            <Fragment>

<div className="container">
  {this.state.loading ?
     <Lottie
        options={defaultOptions}
        height={"100%"}
        width={"100%"}
      /> 



  :
  
  <div className="row justify-content-center">

  <div className="col-xl-10 col-lg-12 col-md-9">
    {/* <div class="elesarrloading"></div> */}

    <div className="card o-hidden border-0 shadow-lg my-5">
      <div className="card-body p-0">
        {/* <!-- Nested Row within Card Body --> */}
        <div className="row">
          <div className="col-lg-6 d-none d-lg-block bg-login-image"></div>
          <div className="col-lg-6">
            <div className="p-5">
              {this.props.error.status === 400 ? 
                      <p style={{color:'red', fontSize:"30px"}} className="text-center">
                        {this.props.error.msg.msg} !!                        </p>

              : null}

              <div className="text-center">
                <h1 className="h4 text-gray-900 mb-4">Reset Password</h1>
              </div>
              <form className="user" onSubmit ={this.confirmEmail} >
                <div className="form-group">
                      <input 
                                                type="email" 
                                                className="form-control form-control-user"
                                                onChange = {this.onChange} 
                                                id="email" 
                                                name="email" 
                                                placeholder="enter email" 
                                                required
                                            />
                </div>
             
               
                <button type="submit" className="btn_3 btn-block text-center">
                  Confirm Email
                </button>
                <hr />
              </form>
              <hr />
              {/* <div className="text-center">
                <a className="small" href="#">Forgot Password?</a>
              </div> */}
              <div className="text-center">
                <Link className="small" to="/signup">Create an Account!</Link>
              </div>
              {/* <div className="text-center">
                <Link className="small" to="/organisation-signup">Create an Organisation Account!</Link>
              </div> */}
              <div className="text-center">
                <Link className="btn mt-2 btn-primary" to="/">
                  Go BACK
                  </Link>
              </div>
          
            </div>
          </div>
        </div>
      </div>
    </div>

  </div>

</div>

  }

{/* <!-- Outer Row --> */}

</div>
            </Fragment>
            )
    }
 
}

const mapStateToProps = state => ({
    auth: state.auth,
    error: state.error
  });
  
  export default connect(mapStateToProps, { login })(
    withRouter(ConfirmEmail)
  );