import axios from "axios";
import { returnErrors } from "./errorActions";
import {  
      TX_LOADED,
      TX_LOADING
      } from "./types";




var location = window.location.href
if(location.slice(0,17)== 'http://localhost:'){
  axios.defaults.baseURL = 'https://elesarrdevelopment.herokuapp.com';
}else{
  axios.defaults.baseURL = 'https://eles-api.herokuapp.com'
}




export const getTX = (id) => dispatch => {
    dispatch({
      type:  TX_LOADING
    });
  
    axios
      .get(`api/signupuser/${id}/tx`)
      .then(res =>{

        dispatch({
          type: TX_LOADED,
          payload: res.data
        })
        // console.log(res.data)
      }
      )
      .catch(err => {
        // dispatch(
        //   returnErrors(err.response.data, err.response.status, "REGISTER_FAIL")
        // );
        console.log(err);
        // dispatch({
        //   type: CREATE_CAMPAIGN_FAILED
        // });
      });
  };
