import React, { Component } from 'react';
// import Fragment from "../../../hoc/Fragment"
import Fragment from "../../../hoc/Fragment"
// import "../assets/css/dashboard.css"
// import "../assets/css/bootstrap.min.css"
import ImageUploader from 'react-images-upload';
import {connect} from 'react-redux'
import { 
  withRouter,
 } from "react-router-dom"
import {createOrganizationCampaign} from "../../../action/campaignActions"


import Lottie from "react-lottie";
import * as done from '../../../loaders/done.json'

import DraftJsRichEditorExample from "./DraftJsRichEditorExample"
import { stateToHTML } from "draft-js-export-html";
import Modal from "react-awesome-modal";

import {
  EditorState,
  convertFromHTML as convertFromHTMLClassic,
} from 'draft-js';



const defaultOptions = {
  loop: true,
  autoplay: true,
  animationData: done.default,
  rendererSettings: {
    preserveAspectRatio: "xMidYMid slice",
  },
};

class ProjectForm extends Component {

    state = {
        project_name:null,
        project_description:null,
        image_slide_1:[],
        image_slide_2:[],
        image_slide_3:[],

        pitch_deck:null,
        fb_url:null,
        linkedin:null,
        inst_url:null,
        video_url:null,
        project_deadline:null,
        project_goal:null,  
        useCryptoValue:false,
        theme: 'snow',
        editorState: EditorState.createEmpty(),
        loading:false,
        submit : "submit",
        visible : false,
        data: '',
        text:'',
    }


        
    onChange = e => {
        this.setState({useCryptoValue:true})
          this.setState({ [e.target.name]: e.target.value });
        };
  
        handleChange  = editorState => {
          this.setState({
            editorState,
            editorContentHtml: stateToHTML(editorState.getCurrentContent())
          
          });
        };

        /** MODAL */
        closeModal() {
          this.setState({
            visible: false
          });
    
          window.location.href ="/organisation/dashboard"
        }
        /** END MODAL */
        /**
         * IMAGE UPLOAD
         * 
         */
            onDrop1 = (picture) =>{
              this.setState({
                image_slide_1: this.state.image_slide_1.concat(picture),
              })
          }
        
          // IMAGE SLIDE 2
          onDrop2 = (picture) =>{
            this.setState({
              image_slide_2: this.state.image_slide_2.concat(picture),
            })
        }
        
        
        // IMAGE SLIDE 3
        onDrop3 = (picture) =>{
          this.setState({
            image_slide_3: this.state.image_slide_3.concat(picture),
          })
        }
         /** END IMAGE UPLOAD */

        componentDidUpdate(prevProps){


        console.log("prev props ", prevProps)
  
        if(this.props.campaign.type !== prevProps.campaign.type){
          if(this.props.campaign.type === "CAMPAIGN_CREATED"){
            // console.log("component updated to true")
            // console.log(this.props.campaign.type)
            this.setState({
              visible : true
            })
          }
        }
      }

    handleSubmit= async e => {
      this.setState({submit : "creating campaign..."})

/**
 *  HANDLES SUBMISSION OF THE FORM
 *    * Project Name
 *    * Project Description
 *    * Category
 *    * Project Goal 
 *    * Project Deadline
 *    * Facebook Link
 *    * Linkedin Link
 *    * Instagram Link
 *    * User ID
 *    * User Email
 *    * User Username
 *    * Use Crypto
 *    * Pitch Deck
 *    * Image Slide 1
 *    * Image Slide 2
 *    * Image Slide 3
 */

        e.preventDefault()
        this.setState({loading:true})
        const {user}= this.props.auth
        var newDeadline=new Date(this.project_deadline.value)
        newDeadline= newDeadline.getTime()
        const fd= new FormData()
        // console.log(user.id.toString())
              fd.append("image_slide_1",this.state.image_slide_1[0])
              fd.append("image_slide_2",this.state.image_slide_2[0])
              fd.append("image_slide_3",this.state.image_slide_3[0])
              // fd.append("pitch_deck",this.pitch_deck.files[0], this.pitch_deck.files[0].name)
              fd.append("userID", user.id)
              fd.append("project_name",this.project_name.value)
              fd.append("project_description", this.state.editorContentHtml)
              fd.append("fb_url",this.fb_url.value)
              fd.append("linkedin",this.linkedin.value)
              fd.append("inst_url",this.inst_url.value)
              fd.append("video_url",this.video_url.value)              
              fd.append("project_deadline", newDeadline)
              fd.append("project_goal",this.project_goal.value)
              fd.append("useCrypto",this.state.useCryptoValue)
              
              fd.append("username",user.name_of_organisation)
              fd.append("email",user.email)
              fd.append("category",this.category.value)
        await this.props.createOrganizationCampaign(fd)
        this.setState({loading:false})
      }
      
    render() {
        return (
            <Fragment>
              {this.state.loading ? 
              <Lottie
              options={defaultOptions}
              height={"100%"}
              width={"100%"}
              /> 
              :
          <div class="card shadow mb-4">
          <div class="card-header py-3">
            <h6 class="m-0 font-weight-bold text-primary">CREATE CAMPAIGNS</h6>
          </div>
          <div class="card-body">
          <form action="" onSubmit={this.handleSubmit} enctype="multipart/form-data">  
              <div className="row">
                  <div className="col-md-6">
                    <label for="option" >CAMPAIGN CATEGORY:</label>
                    <select
                    className="custom-select  mb-5"
                      required 
                        name="" id=""  ref={(input) => this.category = input}>
                          
                          <option value="CREATIVES">CREATIVES</option>
                    <option value="NGO">NGO</option>
                    <option value="CHARITY">CHARITY</option>
                    <option value="HEALTH">HEALTH</option>
                    <option value="MUSIC">MUSIC</option>
                    <option value="FILM MAKING">FILM MAKING</option>
                    <option value="EDUCATION">EDUCATION</option>
                    <option value="FASHION INDUSTRY">FASHION INDUSTRY</option>  
                    </select>
                </div>
                
              <div className="col-md-6">
                  <label for="project_name">CAMPAIGN NAME</label><br />  
                  <input 
                      type="text" 
                      name="project_name" 
                      id=""
                      className="form-control  mb-5" 
                      placeholder="Enter Campaign Name"
                      ref = {(input)=> {this.project_name=input}}
                      required
                    />
               </div>
            </div>
      
    <div className="mt-3" >
        <label for="project_description">YOUR CAMPAIGN STORY</label><br />
        <DraftJsRichEditorExample
                className="DraftEditor-root"
                editorState={this.state.editorState}
                onChange={this.handleChange}
                placeholder="YOUR CAMPAIGN STORY ....."
              />
  
    </div>
  
    <hr />
    <div className="row mt-5">
  
    <div className="col-md-6">
      <label for="project_dealine">CAMPAIGN DEADLINE:</label>
      <input 
          type="date" 
          name="project_deadline" 
          id="" 
          className="form-control  mb-4"
          placeholder="ENTER CAMPAIGN DEADLINE"
          ref = {(input)=> {this.project_deadline=input}}
          required
        />
    </div>
     
    <div className="col-md-6">
        <label for="number">CAMPAIGN GOAL</label>
        <input 
          type="text"  
          name="project_goal" id="" 
          ref = {(input)=> {this.project_goal=input}}
          required
          placeholder="AMOUNT YOU WANT TO RAISE"
          className="form-control"
        />
      </div>
    </div>
    <div className="row mt-5">
  
  <div className="col-md-6">
  <label for="text">UPLOAD AN IMAGE:JPG OR PNG </label>
  <ImageUploader
                withIcon={true}
                buttonText='Choose images'
                onChange={this.onDrop1}
                imgExtension={['.jpg', '.gif', '.png', '.gif', '.jpeg']}
                maxFileSize={5242880}
                singleImage = {true}
                withPreview = {true}
            />

  </div>

  <div className="col-md-6">
    <label for="text">UPLOAD ANOTHER IMAGE:JPG OR PNG </label>
    <ImageUploader
                withIcon={true}
                buttonText='Choose images'
                onChange={this.onDrop2}
                imgExtension={['.jpg', '.gif', '.png', '.gif', '.jpeg']}
                maxFileSize={5242880}
                singleImage = {true}
                withPreview = {true}

            />
    </div>

  </div>    
  <div className="row mt-5">
    
    <div className="col-md-6">
    <label for="text">UPLOAD ANOTHER IMAGE :JPG OR PNG </label>
    <ImageUploader
                withIcon={true}
                buttonText='Choose images'
                onChange={this.onDrop3}
                imgExtension={['.jpg', '.gif', '.png', '.gif', '.jpeg']}
                maxFileSize={5242880}
                singleImage = {true}
                withPreview = {true}

            />
    </div>
    <div className="col-md-6">
          <label for="fb_url">FACEBOOK PAGE</label>
    <input 
      type="url"
      className="form-control" 
      name="fb_url" 
      id=""
      ref = {(input)=> {this.fb_url=input}}  
      placeholder="ENTER YOUR FACEBOOK PAGE LINK" 
      required
      />
  </div>
  
  </div>
  
         {/* SOCIAL LINKS */}
    <div className="row mt-5">
    
  <div className="col-md-6">
        
  <label for="linkedin">Twitter Page</label>
  <input
  className="form-control  mb-4" 
  type="url" 
  name="linkedin" 
  id="linkedin"
  required 
  ref = {(input)=> {this.linkedin=input}}
  placeholder="ENTER YOUR LINKEDIN PROFILE LINK" />
  </div>    
  <div className="col-md-6">
  <label for="inst_url">Instagram Page</label>
  <input 
  required
  className="form-control"
  type="url" 
  name="inst_url" 
  id="inst_url" 
  ref = {(input)=> {this.inst_url=input}}
  placeholder="ENTER YOUR INSTAGRAM PAGE LINK" 
  />
  </div>
  
    </div>
  
  <div className="row mt-5 mb-5">
 
  <div className="col-md-6">
  <label for="video_url">VIDEO LINK: OPTIONAL</label>
  <input 
  required
  type="url" 
  name="video_url" 
  className="form-control"
  id="" 
  ref = {(input)=> {this.video_url=input}}
  placeholder="ENTER YOUTUBE LINK" 
  />
  </div>
  
  </div>
  
     {/* <div >
        <label>Use Crypto</label>
        <br />
      <div className="inline">
        <label for="">Yes
        <input
          type="checkbox" 
          className="form-control"
          id="useCrypto" 
          name="useCrypto"
          ref = {(input)=> {this.useCrypto=input}}
          onChange={this.onChange}
          value="" />
        </label>
      </div>
    </div> */}
    {/* // checked={this.state.useCrypto}
          // onClick={(e)=>{
          //   e.preventDefault()
          //   this.setState({useCrypto:true})
          // } } */}
  
  
    <hr className="hr" />
    <input className="btn btn-primary  btn-block" type="submit" value={this.state.submit}  />
  </form>
              </div>

              <Modal
                    visible={this.state.visible}
                    width="60%"
                    height="80%"
                    effect="fadeInLeft"
                    onClickAway={() => {
                      this.closeModal();
                      // this.setState({ msg: "" });
                    }}
                  >
                    (
                      <div className = "container">
                   
                        <center>
                          <h2>Campaign Created Successfully</h2>
                        

                          <Lottie
                        options={defaultOptions}
                        height={"50%"}
                        width={"50%"}
                      />
<br></br>
<button
                            className="btn btn-danger"
                            value="Close"
                            onClick={() => this.closeModal()}
                          >
                            Close
                          </button>
                        </center>

                    
              
                     
                     
                        
                      </div>
                
                  </Modal>

            </div>
  
              }
          
            </Fragment>

        );
    }
}

 

const mapStateToProps = state => ({
    auth: state.auth,
    error: state.error,
    campaign:state.campaign
  });
  
  // export default ProjectForm
  export default connect(mapStateToProps,{createOrganizationCampaign})(
    withRouter(ProjectForm)
  );