import React, { Component } from 'react';
import { 
    Link,
    withRouter,
   } from "react-router-dom"
import store from '../../../store'
import {connect} from 'react-redux'
import {getPayoutRequests} from "../../../action/payoutAction"


class Payout extends Component {
    componentDidMount(){
        this.props.getPayoutRequests()
    }
    render() {
        const {payouts} = this.props.payout
        return (
            <React.Fragment>
                            <div class="card shadow mb-4">
            <div class="card-header py-3">
              <h6 class="m-0 font-weight-bold text-primary">PAYOUT REQUESTS</h6>
            </div>
            <div class="card-body">

                  <div class="table-responsive">
                      <table class="table"  width="100%" cellspacing="0">
                        <thead class="thead-dark">
                          <tr>
                            <th>USER ID</th>
                            <th>PROJECT ID</th>
                            <th>EMAIL </th>
                            <th>AMOUNT</th>
                            <th>BANK NAME</th>
                            <th>DATE</th> 
                            </tr> 
                        </thead>
                        <tbody>
                            {payouts && payouts.map((payout, key)=>{
                                return(
                                    <tr>
                                    <td>
                                    <Link  to={{
                                        // pathname: `${`/admin/dashboard/organisation/${organisation._id}`}`,
                                        // state: {
                                        //   content: `${organisation._id}`,
                                        //   adminID:`${admin.id}`
                                        // }
                                      }}
                                      >
                                        {payout.userID} 
                                      </Link>
                                      
                                      </td>
                                    <td>{payout.projectID}</td>
                                    <td>{payout.email}</td>
                                    <td>{payout.amount}</td>
                                    <td>{payout.bankName}</td>
                                    <td>{payout.date_created}</td>
                                    
                                </tr>   
                                )
                            })}
      
                      </tbody>
      
                      </table>
      
                    </div>
      
            
            </div>
          </div>

            </React.Fragment>
        );
    }
}



const mapStateToProps = state => ({
    auth: state.auth,
    error: state.error,
    admin:state.admin,
    payout:state.payout
  });

  
  // export default ProjectForm
  export default connect(mapStateToProps,{
    getPayoutRequests
  })(
    withRouter(Payout)
  );
  
