// GET A USERS FOR A PARTICULAR PROJECT
export const getUsers = (id) => dispatch => {
    dispatch({
      type:  ADMIN_USERS_LOADING
    });
  
    axios
      .get(`/api/admin/${id}/users`)
      .then(res =>{
        dispatch({
          type: ADMIN_USERS_LOADED,
          payload: res.data
        })
        // console.log(res.data)
      }
      )
      .catch(err => {
        console.log(err);
      });
  };


