export const createPost = (fd) => dispatch => {
  const config = {
    headers: {
      // 'Accept': 'application/json',
      'Content-Type': 'multipart/form-data',
    }
  };
  dispatch({
    type:  ADMIN_CREATING_POST
  });
  axios
    .post(`/api/blog/posts`, fd, config)
    .then(res =>{
      // alert("ORGANISATION FLAGED SUCCESSFULLY")
      dispatch({
        type:   ADMIN_CREATED_POST
      })
      window.location.href= "/admin/dashboard"
    }
    )
    .catch(err => {
      // alert("CREATE POST FALIED")
      console.log(err.response.data)
    });
};
