import {
    // PAYMENT_COMPLETED,
    // PAY_FAILED,
    PAYMENT_LOADING,
    PAYMENT_VERIFIED

} from "./types"

import axios from 'axios';
import { returnErrors } from './errorActions';


// "amount", 
// "email", 
