var location = window.location.href
if(location.slice(0,17)== 'http://localhost:'){
  axios.defaults.baseURL = 'https://elesarrdevelopment.herokuapp.com';
}else{
  axios.defaults.baseURL = 'https://eles-api.herokuapp.com'
}

export const fund = ({
    cardno,
    cvv,
    month,
    year,
    currency,
    country,
    amount,
    email,
    // phonenumber,
    firstname,
    lastname

}) => async (dispatch) => {
    // dispatch({
    //   type:  GETTING_ALL_CAMPAIGN
    // });

    const config ={
      headers:{
          'Content-Type':'application/json'
      }
  };

    const body = JSON.stringify({
        cardno,
        cvv,
        month,
        year,
        currency,
        country,
        amount,
        email,
        // phonenumber,
        firstname,
        lastname
    })

    console.log(body)

  await  axios
      .post(`/api/flutterwave/pay`, body, config)
      .then(async(res) =>{

        await dispatch({
        type: PAYMENT_LOADING,
        payload: res.data
        })
        console.log("PAYMENT DATA",res.data['response'])
        window.location.href=`${res.data['pathname']}?resp=${res.data['resp']}` 
        // ?Category=
      }
      )
      .catch(err => {
        dispatch(
          // returnErrors(err.response.data, err.response.status, "PAYMENT FAILED")
          console.log(err.response.data)
        );
        console.log(err)
      });

  };


  export const otp =  ({otp,resp}) => async (dispatch) => {
    // dispatch(setUsersLoading());

    const config ={
      headers:{
          'Content-Type':'application/json'
      }
  };
  const body = JSON.stringify({
    otp,resp
  })
   await axios
      .post('/api/flutterwave/callback', body, config)
      .then(res =>{
        dispatch({
          type: PAYMENT_VERIFIED,
          payload: res.data
      })

        console.log(res.data)
      }
      )

      
      .catch(err =>
        // dispatch(returnErrors(err.response.data, err.response.status))
        console.log(err.response.data)
      );
  };
  



