import {
    ADMIN_USERS_LOADED,
    ADMIN_USERS_LOADING,
    ADMIN_USERS_LENGTH_LOADING,
    ADMIN_USERS_LENGTH_LOADED,
    ADMIN_USER_DETAIL_LOADING,
    ADMIN_USER_DETAIL_LOADED,

    ADMIN_DELETING_USER,
    ADMIN_DELETED_USER,

  ADMIN_FLAGING_USER,
  ADMIN_FLAGED_USER,
  ADMIN_FLAGGED_USERS_LENGTH_LOADED,
  ADMIN_FLAGGED_USERS_LENGTH_LOADING,
  ADMIN_FLAGGED_USERS_LOADED,
  ADMIN_FLAGGED_USERS_LOADING,
  ADMIN_PROJECT_TOTALBALANCE_LOADING,
  ADMIN_PROJECT_TOTALBALANCE_LOADED,

  ADMIN_PROJECTS_LENGTH_LOADED,
ADMIN_PROJECTS_LENGTH_LOADING,

ADMIN_ORGANISATIONS_LENGTH_LOADED,
ADMIN_ORGANISATIONS_LENGTH_LOADING,

ADMIN_ORGANISATIONS_LOADED,
ADMIN_ORGANISATIONS_LOADING,

ADMIN_FLAGING_ORGANISATION,
ADMIN_FLAGED_ORGANISATION,
ADMIN_DELETING_ORGANISATION,
ADMIN_DELETED_ORGANISATION,
ADMIN_SINGLEORGANISATION_LOADING,
ADMIN_SINGLEORGANISATION_LOADED,

ADMIN_PROJECTS_LOADED,
ADMIN_PROJECTS_LOADING,
ADMIN_PROJECTS_FLAGGING,
ADMIN_PROJECTS_FLAGGED,
ADMIN_PROJECTS_DELETING,
ADMIN_PROJECTS_DELETED,
ADMIN_PROJECTS_DETAIL_LOADING,
ADMIN_PROJECTS_DETAIL_LOADED,

ADMIN_ALL_POSTS_LOADED,
ADMIN_ALL_POSTS_LOADING,
ADMIN_POSTS_LENGTH_LOADED,
ADMIN_POSTS_LENGTH_LOADING,
ADMIN_ALL_SINGLEPOST_LOADED,
ADMIN_ALL_SINGLEPOSTS_LOADING,
ADMIN_ALL_COMMENTS_LOADED,
ADMIN_ALL_COMMENTS_LOADING,
ADMIN_CREATED_POST,
ADMIN_CREATING_POST


  
  } from "../action/types";

  const initialState = {
    //  token:localStorage.getItem("token"),
    //  isAuthenticated:null,
    isLoading: false,
    adminUsers: [],
    adminFlaggedUser:[],
    adminOrganisations:[],
    adminProjects:[],
    adminPosts:[],
    adminSinglePost:'',
    adminPostLength:0,
    adminUserLength:0,
    adminFlagUserLength:0,
    adminProjectsLength:0,
    adminOrganisationLength:0,
    adminUserDetail:'',
    adminOrganisationDetail:'',
    adminProjectDetail:'',
    deleteMessage:'',
    flagMessage:'',
    type:'',
    amountRaised:0
    // camp: [],
    // dashboardsingleCampaign:''
  };
  
  export default function (state = initialState, action) {
    switch (action.type) {
  
      case ADMIN_USERS_LOADED:
        // console.log("ACTION PAYLOAD", action.payload);
        return {
          ...state,
          // isAuthenticated:true,
          isLoading: false,
          adminUsers:action.payload,
          type:"ADMIN_USERS_LOADED",
        };
        case ADMIN_USERS_LOADING:
            return {
              isLoading: true,
            };
        
        /** START ADMIN LENGTH */

        case ADMIN_USERS_LENGTH_LOADED:
            return {
                ...state,
                isLoading: true,
                type:"ADMIN_USERS_LENGTH_LOADED",
                adminUserLength:action.payload
            };
        case ADMIN_USERS_LENGTH_LOADING:
            return {
              isLoading: true,
            };
        /** END USER LENGTH */


        /*** USER DETAIL */
        case ADMIN_USER_DETAIL_LOADED:
          return {
            ...state,
            // isAuthenticated:true,
            isLoading: false,
            adminUserDetail:action.payload
          };

          case ADMIN_USER_DETAIL_LOADING:
            return {
              // isAuthenticated:true,
              isLoading: true
            };
  
        /*** END USER DETAIL */
        

        /** DELETING USER */
        case ADMIN_DELETED_USER:
          return {
            ...state,
            deleteMessage:"USER SUCCESSFULLY DELETED!!!!",
            isLoading: false,
          };
        case ADMIN_DELETING_USER:
          return {
            isLoading: true,
          };
          /**END DELETING USER */

          /** FLAG USER */
          case ADMIN_FLAGED_USER:
            return {
              ...state,

              flagMessage:"USER SUCCESSFULLY FLAGED!!!!",
              isLoading: false,
            };
        case ADMIN_FLAGING_USER:
          return {
            isLoading: true,
          };
          /** END FLAG USER */


          /** GET FLAG USERS */
          case ADMIN_FLAGGED_USERS_LOADED:
            return {
              ...state,
              adminFlaggedUser:action.payload,
              isLoading: false,
            };
          case ADMIN_FLAGGED_USERS_LOADING:
            return {
              isLoading: true,
            };
          /** END FLAG USERS */


          /** GET FLAGGED USER LENGTH  */
          case ADMIN_FLAGGED_USERS_LENGTH_LOADED:
            return {
              ...state,
              adminFlagUserLength:action.payload,
              isLoading: false,
            };
          case ADMIN_FLAGGED_USERS_LENGTH_LOADING:
            return {
              isLoading: true,
            };
          /** END FLAGGED USER LENGTH */

            /** AMOUNT RAISED */
            case ADMIN_PROJECT_TOTALBALANCE_LOADED:
            return {
              ...state,
              amountRaised:action.payload,
              isLoading: false,
            };
          case ADMIN_PROJECT_TOTALBALANCE_LOADING:
            return {
              isLoading: true,
            };
            /**END AMOUNT RAISED */
            case ADMIN_PROJECTS_LENGTH_LOADED:
              return {
                ...state,
                adminProjectsLength:action.payload,
                isLoading: false,
              };
            case ADMIN_PROJECTS_LENGTH_LOADING:
              return {
                isLoading: true,
              };
  
            /** GET TOTAL PROJECTS */

            /** GET TOTAL ORGANISATION */
            case ADMIN_ORGANISATIONS_LENGTH_LOADED:
              return {
                ...state,
                adminOrganisationLength:action.payload,
                isLoading: false,
              };
            case ADMIN_ORGANISATIONS_LENGTH_LOADING:
              return {
                isLoading: true,
              };
            /**END GET TOTAL ORGANISATION */
            

            /** GET ORGANISATIONS */
            case ADMIN_ORGANISATIONS_LOADED:
              return {
                ...state,
                adminOrganisations:action.payload,
                isLoading: false,
              };
            case ADMIN_ORGANISATIONS_LOADING:
              return {
                isLoading: true,
              };
            /**END GET ORGANISATIONS */
            /** GET SINGLE ORGANISATION */
            case ADMIN_SINGLEORGANISATION_LOADED:
              return {
                ...state,
                adminOrganisationDetail:action.payload,
                isLoading: false,
              };
            case ADMIN_SINGLEORGANISATION_LOADING:
              return {
                isLoading: true,
              };
            /**END GET ORGANISATIONS */

          
          
            /** FLAG ORGANISATION */
            case ADMIN_FLAGED_ORGANISATION:
              return {
                ...state,
  
                flagMessage:"USER SUCCESSFULLY FLAGED!!!!",
                isLoading: false,
              };
          case ADMIN_FLAGING_ORGANISATION:
            return {
              isLoading: true,
            };
            /** END FLAG ORGANISATION */
            

                    /** DELETING ORGANISATION */
        case ADMIN_DELETED_ORGANISATION:
          return {
            ...state,
            deleteMessage:"USER SUCCESSFULLY DELETED!!!!",
            isLoading: false,
          };
        case ADMIN_DELETING_ORGANISATION:
          return {
            isLoading: true,
          };

          /**END DELETING ORGANISATION */


      /** GETTING  PROJECTS */
        case ADMIN_PROJECTS_LOADED:
          return {
            ...state,
            adminProjects:action.payload,
            isLoading: false,
          };
        case ADMIN_PROJECTS_LOADING:
          return {
            isLoading: true,
          };
          /**END GETTING PROJECTS */


          /** FLAG PROJECT */
        case ADMIN_PROJECTS_FLAGGED:
          return {
            ...state,

            flagMessage:"USER SUCCESSFULLY FLAGED!!!!",
            isLoading: false,
          };
        case ADMIN_PROJECTS_FLAGGING:
        return {
          isLoading: true,
        };
          /** END FLAG PROJECT */
          /** DELETING PROJECT */
          case ADMIN_PROJECTS_DELETED:
            return {
              ...state,
              deleteMessage:"PROJECT SUCCESSFULLY DELETED!!!!",
              isLoading: false,
            };
          case ADMIN_PROJECTS_DELETING:
            return {
              isLoading: true,
            };
            /**END DELETING PROJECT */


            /*** USER DETAIL */
        case ADMIN_PROJECTS_DETAIL_LOADED:
          return {
            ...state,
            // isAuthenticated:true,
            isLoading: false,
            adminProjectDetail:action.payload
          };

          case ADMIN_PROJECTS_DETAIL_LOADING:
            return {
              // isAuthenticated:true,
              isLoading: true
            };
  
        /*** END USER DETAIL */
   
        /** GET POSTS */
      case ADMIN_ALL_POSTS_LOADED:
        return {
          ...state,
          // isAuthenticated:true,
          isLoading: false,
          adminPosts:action.payload
        };

        case ADMIN_ALL_POSTS_LOADING:
          return {
            // isAuthenticated:true,
            isLoading: true
          };
        /** END GET POSTS */
        /** GET POSTS LENGTH */
        case ADMIN_POSTS_LENGTH_LOADED:
          return {
            ...state,
            adminPostLength:action.payload,
            isLoading: false,
          };
        case ADMIN_POSTS_LENGTH_LOADING:
          return {
            isLoading: true,
          };
        /** END POST LENGTH */
        case ADMIN_ALL_SINGLEPOST_LOADED:
          return {
            ...state,
            adminSinglePost:action.payload,
            isLoading: false,
          };
        case ADMIN_ALL_SINGLEPOSTS_LOADING:
          return {
            isLoading: true,
          };
        
      case ADMIN_CREATED_POST:
        return {
          ...state,
          adminPosts:action.payload,
          isLoading: false,
        };
      case ADMIN_CREATING_POST:
        return {
          isLoading: true,
        };
      default:
        return state;
    }
  }
  
  