import React from 'react';
import Fragment from "../../../hoc/Fragment"
import { 
  Link,
  withRouter,
 } from "react-router-dom"
 import store from '../../../store'
 
import * as CampaingLoading from '../../../loaders/loading.json'
import {
  getProjects,
  flagProject,
  deleteProject,
  getProjectDetail,
  getTotalProjects,
  getAmountRaised
} from  "../../../action/adminAction"

import {connect} from 'react-redux'
import "../assets/css/dataTables.bootstrap4.min.css"
import Lottie from "react-lottie";


const defaultOptions = {
  loop: true,
  autoplay: true,
  animationData: CampaingLoading.default,
  rendererSettings: {
    preserveAspectRatio: "xMidYMid slice",
  },
};

class Projects extends React.Component{
  componentDidMount() {
    console.log("IN THE PAGE RN");
    if (this.props.location.state) {
      store.dispatch(getProjects(this.props.location.state.content));
      store.dispatch(getTotalProjects());
      store.dispatch(getAmountRaised());

          }
  }

 state = {
   loading : false
 }

  render(){
    const {isAuthenticated}= this.props.auth
    const {amountRaised, adminProjects,adminProjectDetail,adminProjectsLength}=this.props.admin
    const admin =this.props.auth.user
    var raised;
    if(amountRaised){
      console.log(amountRaised['0'])
      raised=amountRaised['0'].count

    }
      return(
        <Fragment>
      <div class="card shadow mb-4">
              <div class="card-header py-3">
      <h6 class="m-0 font-weight-bold text-primary">{adminProjectsLength} CAMPAIGNS    {raised}Raised</h6>
              </div>
              <div class="card-body">
  
                {this.state.loading ?    
                <div className = "container"> 
                        <Lottie
                          options={defaultOptions}
                          height={"100%"}
                          width={"100%"}
                        />
                        </div>
                      :   <div class="table-responsive">
                        <table class="table"  width="100%" cellspacing="0">
                          <thead class="thead-dark">
                            <tr>
                            <th>Project Name</th>
                        <th>Email</th>
                        <th>Project Created</th>
                        <th>Project Goal</th>
                        <th>Amount Raised</th>
                        <th>Edit</th>
                        <th>Delete</th>                      
                            </tr>
                          </thead>
                          <tbody>
                          {
                        
                        adminProjects?
    
                     
    
    
                        adminProjects.map((project, key)=> {
                        return(
  
                            <tr key={key}>
                            <td>
                              <Link  to={{
                                pathname: `${`/admin/dashboard/${project._id}/project`}`,
                                state: {
                                  content: `${project.project_deadline}`,
                                  adminID:`${admin.id}`

                                }
                              }}
                              >
                                {project.project_name}
                              </Link>
                              </td>
                            <td>{project.email}</td>
                            <td className="process">
                            {new Intl.DateTimeFormat("en-GB", {
                                        year: "numeric",
                                        month: "long",
                                        day: "2-digit"
                                    }).format(project.date_created)}
                              </td>                      
                              <td> {project.project_goal}</td> 
                              <td> {project.project_current_balance}</td>
                                    
                            <td>
  
            <Link
            onClick={(e)=> {
              e.preventDefault()
              console.log("ADMIN",admin.id)
              console.log("USER ID",project._id)
               const det ={
                   adminId:admin.id,
                   projectID:project._id

                 }
                this.props.flagProject(det)
            }}
            >
              <i className="zmdi zmdi-edit"></i>
            </Link>
                            </td>
                            <td>
                            <Link 
                              href="#"
                               onClick={(e)=> {
                                 e.preventDefault()
                                 console.log("ADMIN",admin.id)
                                 console.log("USER ID",project._id)
                                  const det ={
                                      adminId:admin.id,
                                      projectID:project._id
                  
                                    }
                                   this.props.deleteProject(det)
                               }}
                              >
                              <i className="zmdi zmdi-delete"></i></Link>
                            
                            </td>
        
                            <td>
                        
                            </td>
                            </tr>    
        
                        )}) : <tr>NO PROJECT</tr>}
                        
                        </tbody>
        
                        </table>
        
                      </div>
        }
              
              </div>
            </div>
       </Fragment>
    )



   
  }
    
}


const mapStateToProps = state => ({
  auth: state.auth,
  error: state.error,
  admin:state.admin
});

// export default ProjectForm
export default connect(mapStateToProps,{
  getProjects,
  deleteProject,
  flagProject
})(
  withRouter(Projects)
);


