import React, { Component } from 'react';
// import Fragment from "../../../hoc/Fragment"
import Fragment from "../../../hoc/Fragment"

// import "../assets/css/dashboard.css"
import * as CampaingLoading from '../../../loaders/loading.json'
// import "../assets/css/table.css"
import {getOrganizationCampaign, deleteOrgCampaign} from  "../../../action/campaignActions"
import Lottie from "react-lottie";
import {connect} from 'react-redux'
import { 
  Link,
    withRouter,
   } from "react-router-dom"
  import store from '../../../store'



  
const defaultOptions = {
  loop: true,
  autoplay: true,
  animationData: CampaingLoading.default,
  rendererSettings: {
    preserveAspectRatio: "xMidYMid slice",
  },
};

class Campaigns extends Component {


  state = {
    loading : false
  }
    async componentDidMount(){
            //  this.props.getAllCampaign()
            // await sleep(6000)
            this.setState({
              loading :true
            })
        
        
            const {user}= this.props.auth
            console.log(user.id)
            // store.dispatch(getCampaign(this.props.match.params.id))

            store.dispatch(getOrganizationCampaign(this.props.match.params.id))
            setTimeout(() => {
              this.setState({
                loading :false
              })
            }, 2000);
    }
    render() {
        // console.log("RENDER:",this.props.auth.user)
        // console.log("PROPS CAMPAIGN",this.props.campaign.orgCampaigns)

        return (
            <Fragment>
                  <div class="card shadow mb-4">
            <div class="card-header py-3">
              <h6 class="m-0 font-weight-bold text-primary">YOUR CAMPAIGNS</h6>
            </div>


          <div class="card-body">

          {this.state.loading ? 
            
            <Lottie
            options={defaultOptions}
            height={"100%"}
            width={"100%"}
          /> : 
          <div class="table-responsive">
            <table  class="table"  width="100%" cellspacing="0">
            <thead class="thead-dark">
                <tr>
                <th>Campaign Name</th>
                <th>Image Slide 1</th>
                <th>Image Slide 2</th>
                <th>Image Slide 3</th>
                <th>Campaign Creation Date</th>
                <th>Campaign Deadline</th>
                
                <th>Campaign Goal</th>
                <th>Amount Raised</th>
                <th>Edit</th>
                <th>Delete</th>
                </tr>
              </thead>
              <tbody>
              {this.props.campaign.orgCampaigns ? this.props.campaign.orgCampaigns.map((project, key)=> { 

return(

                <tr key={key} >
                <td>
                <Link to={{
                  pathname: `${`/organisation/dashboard/${project._id}/project`}`,
                  state: {
                    content: `${project.project_deadline}`
                  }
                }}>
                  {project.project_name}</Link></td>
                <td><img width="100" height="100" src={project.image_slide_1}/></td>
                <td><img width="100" height="100" src={project.image_slide_2}/></td>
                <td><img width="100" height="100" src={project.image_slide_3}/></td>
                <td className="process">
                          {new Intl.DateTimeFormat("en-GB", {
                                      year: "numeric",
                                      month: "long",
                                      day: "2-digit"
                                  }).format(project.date_created)}
                            </td>    
                            <td className="process">
                          {new Intl.DateTimeFormat("en-GB", {
                                      year: "numeric",
                                      month: "long",
                                      day: "2-digit"
                                  }).format(project.deadline)}
                            </td>    
                            
                <td>{project.project_goal}</td>
                <td>{project.project_current_balance}</td>
                <td>
                {/* <Link href="#">
                  </Link> */}

                <Link to={{
        pathname: `${`/organisation/dashboard/${project._id}/edit-campaign`}`,
        state: {
          content: `${project.project_description}`
        }
      }}><i className="zmdi zmdi-edit"></i>
      </Link>
                </td>
                <td>
                <Link 
                  href="#"
                  onClick={(e)=> {
                    const {user}= this.props.auth
                    console.log(user.id)
                    console.log(project._id)
                    const det ={
                      userId:user.id,
                      id:project._id
                    }
                    this.props.deleteOrgCampaign(det, this.props.history)
                  }}>
                    <i className="zmdi zmdi-delete"></i>
                  </Link>
                </td>

                <td>
                </td>
                </tr>    
            )}) : <tr><td>NO PROJECT</td></tr>}                
                </tbody>

            </table>
                
            {this.props.campaign.orgCampaigns.length ===0 ?
                <Link to="/organisation/dashboard/create-campaign" width="100%" class="btn btn-primary btn-block">CREATE CAMPAIGN</Link>
                : null}
          </div>
          }
        
        </div>
      
          
          
          
          </div>
  
          </Fragment>
              
        );
    }
}


const mapStateToProps = state => ({
    auth: state.auth,
    error: state.error,
    campaign:state.campaign
  });
  
  // export default ProjectForm
  export default connect(mapStateToProps,{getOrganizationCampaign,deleteOrgCampaign})(
    withRouter(Campaigns)
  );
  
  

