import React, { Component } from 'react'
import { withRouter } from 'react-router'
import axios from 'axios'


// import withRouter from 'react-router-dom'
class Verify extends Component{


    state = {
email : "",
userID : "",
phoneNumber : "",
apiKey : ""
    }
    componentDidMount(){
        const queryString = window.location.search;

const urlParams = new URLSearchParams(queryString);

        const email = urlParams.get('email')
        const userID = urlParams.get('userID')
        const phoneNumber = urlParams.get('phoneNumber')


        this.setState({

            email : email,
            userID : userID,
            phoneNumber : phoneNumber
        })


    }


    verify = () => {

        const config ={
            headers:{
                'Content-Type':'application/json'
            }
        };
        const {email, userID,phoneNumber, apiKey} = this.state
        
        const details = {
            email,userID,phoneNumber, apiKey
        }

        // console.log("details ", details)

        const body = JSON.stringify(details)

        axios.post("https://eles-api.herokuapp.com/api/verify/verify", body, config).then(
            res =>{
                console.log("res.data ", res.data)
                alert(res.data)
            }
        ).catch(err =>{
            console.log(err.response.data)
            alert(err.response.data)
        })
    }
    render(){
        return(
            <div className = "container">

                <center>


<div className="row justify-content-center">

  <div className="col-xl-10 col-lg-12 col-md-9">
    {/* <div class="elesarrloading"></div> */}

    <div className="card o-hidden border-0 shadow-lg my-5">
      <div className="card-body p-0">
        {/* <!-- Nested Row within Card Body --> */}
        <div className="row">
          {/* <div className="col-lg-6 d-none d-lg-block bg-login-image"></div> */}
          <div className="col-lg-12">
            <div className="p-5">
           
              <div className="text-center">
                <h1 className="h4 text-gray-900 mb-4">Verify User!</h1>
              </div>
              <form className="user"  >
                <div className="form-group">
                      <input 
                                                type="text" 
                                                className="form-control form-control-user"
                                                placeholder ="Enter your api key" 
                                               
                                                value={this.state.email}
                                                disabled
                                                required
                                            />
                </div>
                <div className="form-group">
                <input 
                type="password" 
                className="form-control form-control-user"   

                onChange = {(e) =>{
                    this.setState({
                        apiKey : e.target.value
                    })
                }}
                // onChange = {this.onChange} id="password" name="password" 
                placeholder="Enter Your API Key" />
                </div>
                
                <button type="submit" onClick = { (e)=>{
e.preventDefault()
    this.verify()
}}  className="btn_3 btn-block text-center">
                  Verify
                </button>
                <hr />
              </form>
              <hr />
      
            
            </div>
          </div>
        </div>
      </div>
    </div>

  </div>

</div>


                </center>

            </div>
        )
    }
}

export default withRouter(Verify)