import {
    TX_LOADED,
    TX_LOADING
  } from "../action/types";
import axios from "axios";

const initialState = {
//  token:localStorage.getItem("token"),
//  isAuthenticated:null,
isLoading: false,
tx:[]
};
  
  export default function (state = initialState, action) {
    switch (action.type) {
  
      case TX_LOADED:
        // console.log("ACTION PAYLOAD", action.payload);
        return {
          ...state,
          // isAuthenticated:true,
          isLoading: false,
          tx:action.payload
        };
        case TX_LOADING:
            return {
              ...state,
              isLoading: true,
            };  
      default:
        return state;
    }
  }
  