import {    
    PAYOUT_CREATING_LOADING,
    PAYOUT_CREATED_LOADED,
    PAYOUT_LOADING,
    PAYOUT_LOADED,
    PAYOUT_LENGTH_LOADED,
PAYOUT_LENGTH_LOADING
  } from "../action/types";

  const initialState = {
    isLoading: false,
    payouts: [],
    payoutLength:0,
    type:'',

  };
  
  export default function (state = initialState, action) {
    switch (action.type) {
  
      case PAYOUT_LOADED:
        // console.log("ACTION PAYLOAD", action.payload);
        return {
          ...state,
          // isAuthenticated:true,
          isLoading: false,
          payouts:action.payload,
          type:"PAYOUT_LOADED",
        };
        case PAYOUT_LOADING:
            return {
              isLoading: true,
            };
        

    case PAYOUT_CREATED_LOADED:
        // console.log("ACTION PAYLOAD", action.payload);
    return {
        ...state,
        // isAuthenticated:true,
        isLoading: false,
        payouts:action.payload,
        type:"PAYOUT_LOADED",
    };
    case PAYOUT_CREATING_LOADING:
        return {
            isLoading: true,
        };


        case PAYOUT_LENGTH_LOADED:
            // console.log("ACTION PAYLOAD", action.payload);
        return {
            ...state,
            // isAuthenticated:true,
            isLoading: false,
            payoutLength:action.payload,
            type:"PAYOUT_LENGTH_LOADED",
        };
        case PAYOUT_LENGTH_LOADING:
            return {
                isLoading: true,
            };
    
        

            
        /** START ADMIN LENGTH */

        default:
            return state;
        }
      }
      