import React, { Component } from 'react';
import { 
    Link,
    withRouter,
     } from "react-router-dom"
import {getPosts} from  "../../../action/blogAction"
import {connect} from 'react-redux'
import moment from "moment"

// import SinglePost from '../../admin/components/SinglePost';
    
class List extends Component {
    componentDidMount(){
        window.scrollTo(0, 0);
        this.props.getPosts()
    }
    render() {
        const {posts}=this.props.blog
        console.log("POSTS:",posts)
        console.log("HELLO ")
        return (

            <React.Fragment>
                {posts ? 
                <React.Fragment>
                                 <div class="col-lg-8 mb-5 mb-lg-0">
                                 <div class="blog_left_sidebar">
                                     {posts.map((post, key)=> {
                                         return(
                                            <article class="blog_item" key={key}>
                                            <div class="blog_item_img">
                                                <img 
                                                    class="card-img rounded-0" 
                                                    src={post.image} 
                                                    alt=""
                                                    width="150px" height ="400px"
                                                    />
                                                <a href="#" class="blog_item_date">
                                                    <h3>
                                                    {moment(post.dateCreated).get('date')}
                                                    </h3>
                                                    <p>{moment(post.dateCreated).format('MMMM')}</p>
                                                </a>
                                            </div>
                
                                            <div class="blog_details">
                                                <a class="d-inline-block" href="single-blog.html">
                                                    <h2>
                                                        <Link
                                                        className="d-inline-block" 
                                                        to={{
                                                      pathname: `${`/blog/${post.slug}`}`,
                                                      state: {
                                                        id: `${post._id}`
                                                        
                                                      }
                                                    }}
                                                        >
                                                            {post.title}
                                                        </Link>
                                                        </h2>
                                                </a>
                                                <ul class="blog-info-link">
                                                <li><a href=""><i class="fas fa-clock"></i> 
                        {moment(post.dateCreated).fromNow()}
                        </a></li>
                        <li><a href=""><i class="far fa-comments"></i> {post.comments.length} Comments</a></li>

                                                </ul>
                                            </div>
                                        </article>
                
                
                                         )
                                     })}
                          
                                     <nav class="blog-pagination justify-content-center d-flex">
                                         <ul class="pagination">
                                             <li class="page-item">
                                                 <a href="#" class="page-link" aria-label="Previous">
                                                     <i class="ti-angle-left"></i>
                                                 </a>
                                             </li>
                                             <li class="page-item">
                                                 <a href="#" class="page-link">1</a>
                                             </li>
                                             <li class="page-item active">
                                                 <a href="#" class="page-link">2</a>
                                             </li>
                                             <li class="page-item">
                                                 <a href="#" class="page-link" aria-label="Next">
                                                     <i class="ti-angle-right"></i>
                                                 </a>
                                             </li>
                                         </ul>
                                     </nav>
                                 </div>
                             </div>
                                            <div class="col-lg-4">
                                            <div class="blog_right_sidebar">
                        
                        
                                                <aside class="single_sidebar_widget popular_post_widget">
                                                    <h3 class="widget_title">Recent Post</h3>
                                                    {posts.map((post, key)=> {
                                                        return(
                                                            <div class="media post_item">
                                                            <img 
                                                                src={post.image}
                                                                width="70px" height ="70px"
                                                                alt="post"
                                                                />
                                                            <div class="media-body">
                                                            <Link
                                                        className="d-inline-block" 
                                                        to={{
                                                      pathname: `${`/blog/${post.slug}`}`,
                                                      state: {
                                                        id: `${post._id}`
                                                        
                                                      }
                                                    }}
                                                        >
                                                            {post.title}
                                                        </Link>
                                                                {/* <p>January 12, 2019</p> */}
                                                                <p>created {moment(post.dateCreated).fromNow()}</p>

                                                            </div>
                                                        </div>
                                                        )
                                                    })}

                                                </aside>
                                    
                        
                                            </div>
                                        </div>
                         
                </React.Fragment>            
                : null}
            
            </React.Fragment>
        );
    }
}

const mapStateToProps = state => ({
    auth: state.auth,
    error: state.error,
    blog:state.blog
  });
  // export default ProjectForm
  export default connect(mapStateToProps,{getPosts})(
    withRouter(List)
  );