import axios from "axios";
import { returnErrors } from "./errorActions";
import {
    POST_LOADED,
    POST_LOADING,
    SINGLEPOST_LOADING,
    SINGLEPOST_LOADED,
    CREATING_COMMENT,
    CREATED_COMMENT
} from "./types";
var location = window.location.href
if(location.slice(0,17)== 'http://localhost:'){
  axios.defaults.baseURL = 'https://elesarrdevelopment.herokuapp.com';
}else{
  axios.defaults.baseURL = 'https://eles-api.herokuapp.com'
}






export const getPosts= () => dispatch => {
    dispatch({
      type:  POST_LOADING
    });
    
  
    
    axios
      .get(`api/blog/posts`)
      .then(res =>{
        dispatch({
          type: POST_LOADED,
          payload: res.data
        })
        console.log(res.data)
      }
      )
      .catch(err => {
        console.log(err.response.data);
      });
  };
  


  export const getSinglePost= (slug) => dispatch => {
    dispatch({
      type:  SINGLEPOST_LOADING
    });
    

    
    axios
      .get(`api/blog/post/${slug}/comment`)
      .then(res =>{
        dispatch({
          type: SINGLEPOST_LOADED,
          payload: res.data
        })
      }
      )
      .catch(err => {
        console.log(err.response.data);
      });
  };



  export const createComment = (slug,{content, post, owner, fullName}) => dispatch => {
    const config = {
      headers: {
    // 'Accept': 'application/json',
    'Content-Type': 'application/json'
  },
  };
    const body = JSON.stringify({content, post, owner, fullName});
    dispatch({
      type:  CREATING_COMMENT
    });
    axios
      .post(`/api/blog/post/${slug}/comment`, body, config)
      .then(res =>{
        // alert("ORGANISATION FLAGED SUCCESSFULLY")
        dispatch({
          type:   CREATED_COMMENT
        })
        window.location.href= "."
      }
      )
      .catch(err => {
        // alert("CREATE POST FALIED")
        console.log(err.response.data)
      });
  };
