import {
    CONTRIBUTORS_LOADED,
    CONTRIBUTORS_LOADING,
    CONTRIBUTORS_LENGTH_LOADED,
CONTRIBUTORS_LENGTH_LOADING
  } from "../action/types";
  import axios from "axios";
  
  const initialState = {
    //  token:localStorage.getItem("token"),
    //  isAuthenticated:null,
    isLoading: false,
    contributors:[],
    contributorsLength:0
  };
  
  export default function (state = initialState, action) {
    switch (action.type) {
  
      case CONTRIBUTORS_LOADED:
        // console.log("ACTION PAYLOAD", action.payload);
        return {
          ...state,
          // isAuthenticated:true,
          isLoading: false,
          contributors:action.payload
        };
        case CONTRIBUTORS_LOADING:
            return {
              ...state,
              isLoading: true,
            };  

        case CONTRIBUTORS_LENGTH_LOADED:
          return{
            ...state,
            // isAuthenticated:true,
            isLoading: false,
            contributorsLength:action.payload
          }
          case CONTRIBUTORS_LENGTH_LOADING:
            return{
              ...state,
              isLoading: true,
            }
      default:
        return state;
    }
  }
  