import React from 'react';
import Fragment from "../../../hoc/Fragment"
import Modal from "react-awesome-modal";
import ImageUploader from 'react-images-upload';

import {connect} from 'react-redux'
import { 
  withRouter,
 } from "react-router-dom"
import {createPost} from "../../../action/adminAction"


import Lottie from "react-lottie";

import DraftJsRichEditorExample from "./DraftJsRichEditorExample"
import { stateToHTML } from "draft-js-export-html";

import * as done from '../../../loaders/done.json'

// import JSONTree from 'react-json-tree';
import {
  EditorState,
  convertFromHTML as convertFromHTMLClassic,
} from 'draft-js';


const defaultOptions = {
  loop: true,
  autoplay: true,
  animationData: done.default,
  rendererSettings: {
    preserveAspectRatio: "xMidYMid slice",
  },
};


















class  PostForm extends React.Component{

    state = {
        title:null,
        content:null,
        image:[],
        useCryptoValue:false,
        submit : "submit",
        visible : false,
        data: '',
        text:'',
        theme: 'snow',
        editorState: EditorState.createEmpty(),
      }
    
    closeModal() {
      this.setState({
        visible: false
      });

      window.location.href ="/admin/dashboard"
    }
    // IMAGE SLIDE 1 
    onDrop1 = (picture) =>{
      this.setState({
        image: this.state.image.concat(picture),
      })
  }

  


      handleChange  = editorState => {
        this.setState({
          editorState,
          editorContentHtml: stateToHTML(editorState.getCurrentContent())
        
        });
      };
  
  

      /*
      DRAFT JS
      */


      /*
      END DRAFT JS
      */

     handleSubmit= async e => {
      e.preventDefault()
      this.setState({submit : "creating post..."})
      const {user}= this.props.auth

  
      const fd= new FormData()
            fd.append("image",this.state.image[0])
            fd.append("content", this.state.editorContentHtml)
            fd.append("title", this.title.value)
            fd.append("owner", user.id)
            this.props.createPost(fd)
        // console.log(this.state.image[0])
        // console.log(this.state.editorContentHtml)
        // console.log(this.title.value)
        // console.log(user.id)
        
    }


    componentDidUpdate(prevProps){


      console.log("prev props ", prevProps)

      if(this.props.admin.type !== prevProps.admin.type){
        if(this.props.campaign.type === "ADMIN_POSTED_CREATED"){
          this.setState({
            visible : true
          })
        }
      }
    }
    
    render(){
        return(
            <Fragment>
          <div class="card shadow mb-4">
            <div class="card-header py-3">
              <h6 class="m-0 font-weight-bold text-primary">CREATE POST</h6>
            </div>
            <div class="card-body">
            <form action="" onSubmit={this.handleSubmit} enctype="multipart/form-data">



    {/* PROJECT NAME */}

    <div className="row">

    <div className="col-md-6">
        <label for="project_name">POST TITLE</label><br />
            <input 
                type="text" 
                name="project_name" 
                id=""
                className="form-control mb-3" 
                placeholder="enter post title"
                ref = {(input)=> {this.title=input}}
                required
            />
  </div>
  <div className="col-md-6  mb-3">
        <label for="text">UPLOAD AN IMAGE: JPG OR PNG</label>

        <ImageUploader
                        withIcon={true}
                        buttonText='Choose images'
                        onChange={this.onDrop1}
                        imgExtension={['.jpg', '.gif', '.png', '.gif', '.jpeg']}
                        withPreview = {true}
                        maxFileSize={5242880}

                        singleImage = {true}
                        withPreview = {true}
                    />

</div>

    </div>

    
  <div class="mt-4">
  <label for="project_description">YOUR CAMPAIGN STORY</label><br />
 
        <DraftJsRichEditorExample
              className="DraftEditor-root"
              editorState={this.state.editorState}
              onChange={this.handleChange}
              placeholder="YOUR CAMPAIGN STORY ....."
            />


  </div>
  <div>
  </div>
<hr />

  <hr className="hr" />
  <input className="btn btn-primary  btn-block" type="submit" value={this.state.submit} />
</form>

            </div>


            <Modal
                    visible={this.state.visible}
                    width="60%"
                    height="80%"
                    effect="fadeInLeft"
                    onClickAway={() => {
                      this.closeModal();
                      // this.setState({ msg: "" });
                    }}
                  >
                    (
                      <div className = "container">
                   
                        <center>
                          <h2>Post Created Successfully</h2>
                        

                          <Lottie
                        options={defaultOptions}
                        height={"50%"}
                        width={"50%"}
                      />
<br></br>
<button
                            className="btn btn-danger"
                            value="Close"
                            onClick={() => this.closeModal()}
                          >
                            Close
                          </button>
                        </center>

                    
              
                     
                     
                        
                      </div>
                
                  </Modal>
          </div>

            </Fragment>
        )
    
    }
}



const mapStateToProps = state => ({
  auth: state.auth,
  error: state.error,
  campaign:state.campaign,
  admin:state.admin
});

// export default ProjectForm
export default connect(mapStateToProps,{
    createPost
})(
  withRouter(PostForm)
);