import React from 'react';
import Fragment from "../../hoc/Fragment"
import {registerind} from '../../action/authAction'
import axios from 'axios'
import {connect} from 'react-redux'
import { 
    Link,
    // Route,
    // Switch, 
    withRouter,
    BrowserRouter as Router } from "react-router-dom"

  
import Lottie from "react-lottie";
import * as CampaingLoading from '../../loaders/loading.json'

    
    
    const defaultOptions = {
       loop: true,
       autoplay: true,
       animationData: CampaingLoading.default,
       rendererSettings: {
         preserveAspectRatio: "xMidYMid slice",
       },
     };


class Signup extends React.Component{


  componentDidMount(){
      
    const { error } = this.props.auth;

  }

    state = {
        userName : null,
        email : null,
        password : null, 
        confirmPassword : null,
        terms:false,
        loading:false,
        errMessage:null
    }

    onChangeTerms = e => {
      this.setState({value: e.target.checked})    
    };

    onChange =(e) =>{
        this.setState({
            [e.target.name] : e.target.value
        })
    }

    register = async(e) =>{
        e.preventDefault()
      console.log(this.terms.value)
      
        this.setState({loading:true})
        e.preventDefault()
        this.setState({loading:true})
        const email = this.state.email // get email
        const userName = this.state.userName // username
        const password = this.state.password // password 1
        const confirmPassword = this.state.confirmPassword // password 2
        const newUser = {
            email: email
            ,password:password,
            username : userName,
            confirmPassword : confirmPassword            
        }  // create a user object

        await this.props.registerind(newUser,this.props.history)  // call the register
        this.setState({loading:false})
      
    }

    render(){
        return(
        
            // <!--================login_part Area =================-->
            <Fragment>  
        
<div class="container">

<div class="card o-hidden border-0 shadow-lg my-5">
  <div class="card-body p-0">
    {/* <!-- Nested Row within Card Body --> */}
    <div class="row">
      <div class="col-lg-5 d-none d-lg-block bg-register-image"></div>
      <div class="col-lg-7">
        <div class="p-5">
        {this.props.error.status === 400 ? 
                      <p style={{color:'red', fontSize:"30px"}} className="text-center">
                        {this.props.error.msg.msg} !!                        
              </p>
              : null}
        {this.state.errMessage ? 
        
        <p style={{color:'red', fontSize:"30px"}} className="text-center">
        {this.state.errMessage} !!                        
        </p>

        : null }
          <div class="text-center">
            <h1 class="h4 text-gray-900 mb-4">Create an Account!</h1>
          </div>
          <form class="user" onSubmit = {this.register}>
            <div class="form-group row">
              <div class="col-sm-6 mb-3 mb-sm-0">
              <input 
                    type="text" 
                    class="form-control form-control-user"    
                                    id="name" 
                    onChange = {this.onChange}  
                    name="userName" 
                    required
                                                placeholder="Full Name" />
              </div>
              <div class="col-sm-6">

              <input type="email"
                                  required 
                    class="form-control form-control-user"    
              onChange = {this.onChange} id="email" name="email"
                                                placeholder="Email" />
              </div>
            </div>
            <div class="form-group row">
              <div class="col-sm-6 mb-3 mb-sm-0">
                <input 
                        type="password"
                        required 
                        onChange ={this.onChange}
                        class="form-control form-control-user"
                        name="password"
                        id="exampleInputPassword" placeholder="Password" />
              </div>
              <div class="col-sm-6">
              <input 
                                  required
                    type="password" 
                    class="form-control form-control-user"   
                    id="password" onChange ={this.onChange} name="confirmPassword"
                                                placeholder="Confirm Password" />              </div>
            </div>
            <div className="form-group row">
            <div class="col-sm-6 mb-3 mb-sm-0">

                  <div className="custom-control custom-checkbox small">
                    <input 
                      type="checkbox" 
                      className="custom-control-input" 
                      onChange = {this.onChangeTerms}
                      value={this.state.value}
                      ref = {(input)=> {this.terms=input}}
                      id="customCheck" 
                      required
                      // onClick={(e)=>{
                      //     e.preventDefault()
                      //     this.setState({terms:true})
                      //   } }
                      />
                    <label className="custom-control-label" htmlFor="customCheck">Agree To Our Terms And Condition</label>
                  </div>
                </div>
              </div>
            {this.state.loading=== true ? 
              <button type="submit" class="btn_3 btn-block text-center">
              Register.....

              </button>
    
            : 
            <button 
              
              type="submit" class="btn_3 btn-block text-center">
              Register Account
        </button>
    
            }
            <hr />
            {/* <a href="index.html" class="btn btn-google btn-user btn-block">
              <i class="fab fa-google fa-fw"></i> Register with Google
            </a>
            <a href="index.html" class="btn btn-facebook btn-user btn-block">
              <i class="fab fa-facebook-f fa-fw"></i> Register with Facebook
            </a> */}
          </form>
          <hr />
          <div className="text-center">
                <Link className="small" to="/confirmEmail">Forgot Password?</Link>
              </div>
          <div class="text-center">
            <Link class="small" to="/login">Already have an account? Login!</Link>
          </div>
          {/* <div class="text-center">
            <Link class="small" to="/organisation-signup">Create Business Account</Link>
          </div> */}
          <div className="text-center">
                <Link className="btn mt-2 btn-primary" to="/">
                  Go BACK
                  </Link>
              </div>
          
        </div>
      </div>
    </div>
  </div>
</div>
</div>


            </Fragment>
            )
    }
 
}

const mapStateToProps = state => ({
    auth: state.auth,
    error: state.error
  });
  
  export default connect(mapStateToProps, { registerind })(
    withRouter(Signup)
  );

