export default class Blog extends Component {
    render() {
        return (
            <React.Fragment>
                <Header />
                <section className="blog_area " style={{marginBottom:"0px"}}>
        <div className="container">
            <div className="row">
            <Route exact path="/blog/">
                    <List />
                </Route>
                <Route exact path="/blog/:slug" component={SinglePost} />

           
            
            </div>


            </div>
            </section>
            <Footer />
            </React.Fragment>  
        );
    }
}