import React, { Component } from 'react';
import { 
    Link,
    withRouter,
   } from "react-router-dom"
import store from '../../../store'
import {
    getUsers, 
    deleteUser,
    flagUser,

    getFlaggedUsers
    } from  "../../../action/adminAction"
import {connect} from 'react-redux'

class Users extends Component {
    // compon
    componentDidMount() {
        console.log("IN THE PAGE RN");
        if (this.props.location.state) {
          store.dispatch(getUsers(this.props.location.state.content));
        }
      }
    render() {
        const {adminUsers}=this.props.admin
        const admin =this.props.auth.user
        return (
            <div class="card shadow mb-4">
            <div class="card-header py-3">
              <h6 class="m-0 font-weight-bold text-primary">REGISTERED USERS</h6>
            </div>
            <div class="card-body">

                  <div class="table-responsive">
                      <table class="table"  width="100%" cellspacing="0">
                        <thead class="thead-dark">
                          <tr>
                            <th>Username</th>
                            <th>First Name</th>
                            <th>Last Name</th>
                            <th>Email</th>
                            <th>DOB</th>
                            <th>isVerified?</th>
                            <th>Flag</th>                             
                            <th>Delete</th>                      
                            </tr> 
                        </thead>
                        <tbody>
                            {adminUsers && adminUsers.map((user, key)=>{
                                return(
                                    <tr key={user._id}>
                                    <td>
                                      <Link  to={{
                                        pathname: `${`/admin/dashboard/profile/${user._id}`}`,
                                        state: {
                                          content: `${user._id}`,
                                          adminID:`${admin.id}`
                                        }
                                      }}
                                      >
                                          {user.username}
                                      </Link>
                                      </td>
                                      <td>
                                          {user.profile['firstName']}
                                      </td>                                  
                                    <td>
                                    {user.profile['lastName']}
                                    </td>
                                    <td>
                                        {user.email}
                                    </td>
                
                                    <td>
                                    {new Intl.DateTimeFormat("en-GB", {
                                          year: "numeric",
                                          month: "long",
                                          day: "2-digit"
                                      }).format(user.profile['dateOfBirth'])}
                                    </td>
                                    <td>
                                    {user.verified == false ? "NO" : "YES"}
                                        
                                    </td>
                                    <td>
                                    <Link 
                                      href="#"
                                      onClick={(e)=> {   
                                        e.preventDefault()
                                        console.log("ADMIN",admin.id)
                                        console.log("USER ID",user._id)
                                        const det ={
                                          adminId:admin.id,
                                          userID:user._id
                                        }
                                        this.props.flagUser(det)
                                      }}
                                      >
                                        <i class="fas fa-flag-checkered    "></i>
                                      </Link>
                                    
                                    </td>

                                    <td>
                                    <Link 
                                      href="#"
                                      onClick={(e)=> {   
                                        e.preventDefault()
                                        console.log("ADMIN",admin.id)
                                        console.log("USER ID",user._id)
                                        const det ={
                                          adminId:admin.id,
                                          userID:user._id
                                        }
                                        this.props.deleteUser(det)
                                      }}
                                      >
                                      <i className="zmdi zmdi-delete"></i></Link>
                                    
                                    </td>
                
                                    <td>
                                
                                    </td>
                                    </tr>    
                
                                )
                            })}
                
                      </tbody>
      
                      </table>
      
                    </div>
      
            
            </div>
          </div>

        );
    }
}




const mapStateToProps = state => ({
    auth: state.auth,
    error: state.error,
    admin:state.admin
  });
  
  // // export default ProjectForm
  export default connect(mapStateToProps,{deleteUser,flagUser})(
    withRouter(Users)
  );