import React, { Component } from 'react';
import { 
    Link,
    withRouter,
   } from "react-router-dom"
import store from '../../../store'
import {
    getOrganisations,
    flagOrganisation,
    deleteOrganisation
    } from  "../../../action/adminAction"

import {connect} from 'react-redux'

class Organisations extends Component {
    // compon

    componentDidMount() {
        console.log("IN THE PAGE RN");
        if (this.props.location.state) {
          store.dispatch(getOrganisations(this.props.location.state.content));
        }
      }

    render() {
        const {adminOrganisations}=this.props.admin
        const admin =this.props.auth.user
        return (
            <div class="card shadow mb-4">
            <div class="card-header py-3">
              <h6 class="m-0 font-weight-bold text-primary">REGISTERED ORGANISATIONS</h6>
            </div>
            <div class="card-body">

                  <div class="table-responsive">
                      <table class="table"  width="100%" cellspacing="0">
                        <thead class="thead-dark">
                          <tr>
                            <th>Name Of Organisation</th>
                            <th>Email</th>
                            <th>Telephone Number</th>
                            <th>RC NO</th>
                            <th>Office Address</th>
                            <th>Location Of Company</th>
                            <th>State</th>                             
                            <th>Country</th>
                            <th>Postal Code</th>
                            {/* <th>isVerified?</th> */}
                            <th>Flag</th>                             
                            <th>Delete</th> 
                            </tr> 
                        </thead>
                        <tbody>
                            {adminOrganisations && adminOrganisations.map((organisation, key)=>{
                                return(
                                    <tr>
                                    <td>
                                    <Link  to={{
                                        pathname: `${`/admin/dashboard/organisation/${organisation._id}`}`,
                                        state: {
                                          content: `${organisation._id}`,
                                          adminID:`${admin.id}`
                                        }
                                      }}
                                      >
                                        {organisation.name_of_organisation} 
                                      </Link>
                                      
                                      </td>
                                    <td>{organisation.email}</td>
                                    <td>{organisation.telephone_no}</td>
                                    <td>{organisation.rc_no}</td>
                                    <td>{organisation.office_address}</td>
                                    <td>{organisation.location_of_company}</td>
                                    <td>{organisation.state}</td>
                                    <td>{organisation.country}</td>
                                    <td>{organisation.postal_code}</td>
                                    {/* <td>{organisation.postal_code}</td>                                     */}
                                    <td>
                                    <Link 
                                      href="#"
                                      onClick={(e)=> {   
                                        e.preventDefault()
                                        console.log("ADMIN",admin.id)
                                        console.log("USER ID",organisation._id)
                                        const det ={
                                          adminId:admin.id,
                                          organisationID:organisation._id
                                        }
                                        this.props.flagOrganisation(det)
                                      }}
                                      >
                                        <i class="fas fa-flag-checkered"></i>
                                      </Link>
                                    </td>
                                    <td>
                                    <Link 
                                      href="#"
                                      onClick={(e)=> {   
                                        e.preventDefault()
                                        console.log("ADMIN",admin.id)
                                        console.log("USER ID",organisation._id)
                                        const det ={
                                          adminId:admin.id,
                                          organisationID:organisation._id
                                        }
                                        this.props.deleteOrganisation(det)
                                      }}
                                      >
                                      <i className="zmdi zmdi-delete"></i></Link>
                                    </td>
                                </tr>   
                                )
                            })}
      
                      </tbody>
      
                      </table>
      
                    </div>
      
            
            </div>
          </div>

        );
    }
}




const mapStateToProps = state => ({
    auth: state.auth,
    error: state.error,
    admin:state.admin
  });
  
  // // export default ProjectForm
  export default connect(mapStateToProps,{
        // deleteUser,flagUser
        flagOrganisation,
        deleteOrganisation
    })(
    withRouter(Organisations)
  );